import { getCart, updataCount, delCate } from '@/api/cate'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    addList (state, newList) {
      state.cartList = newList
    },
    checkedAll (state, flag) {
      state.cartList.forEach(item => {
        item.checked = flag
      })
    },
    updataNum (state, { goodsId, goodsNum }) {
      // console.log({ goodsId, goodsNum })
      const res = state.cartList.find(item => item.goods_id === goodsId)
      res.goods_num = goodsNum
    }
  },
  actions: {
    // 获取购物车列表
    async getCartList (context) {
      const { data } = await getCart()
      data.list.forEach(item => {
        item.checked = true
      })
      context.commit('addList', data.list)
    },
    // 改变商品数据
    async changeCateNum (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj
      // console.log(goodsNum, goodsId, goodsSkuId)
      context.commit('updataNum', { goodsId, goodsNum })
      const res = await updataCount(goodsId, goodsNum, goodsSkuId)
      console.log(res)
    },
    // 删除购物车
    async deleteCate (context) {
      const opt = context.getters.optCart
      const cartIds = opt.map(item => item.id)
      await delCate(cartIds)
      context.dispatch('getCartList')
    }
  },
  getters: {
    // 商品总数
    cartTotal (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中商品数
    optCart (state) {
      return state.cartList.filter(item => item.checked)
    },
    // 选中总和
    optCartCount (state, getters) {
      return getters.optCart.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中总价
    optCartPrice (state, getters) {
      return getters.optCart.reduce((sum, item) => {
        return sum + item.goods_num * item.goods.goods_price_min
      }, 0).toFixed(2)
    },
    // 是否全选
    isChecked (state) {
      return state.cartList.every(item => item.checked)
    }
  }
}
