<template>
  <div class="home">
    <!-- 标题 -->
    <van-nav-bar  title="智慧商城" ></van-nav-bar>
    <!-- 搜索栏 -->
    <van-search
    placeholder="请输入搜索关键词"
    input-align="center"
    background="#f1f1f2"
    @click="$router.push('/searchPage')"
    />
    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
    <van-swipe-item v-for="item in bannerList" :key="item.imgUrl"><img :src="item.imgUrl" alt=""></van-swipe-item>

    </van-swipe>
    <!-- 格子导航 -->
    <van-grid  :column-num="5" icon-size="40">
    <van-grid-item v-for="item in bavList" :key="item.imgUrl" :icon="item.imgUrl" :text="item.text" />

    </van-grid>
    <!-- 通知栏 -->
    <van-notice-bar scrollable
    left-icon="volume-o"
    text="智慧商城2.0全新上线，更多新品等你来选~"
    />
    <!-- 主会场 -->
    <div class="main">
      <img src="@/assets/main.png" alt="">
    </div>
    <!-- 猜你喜欢 -->
    <div class="guess">
      <p>—— 猜你喜欢 ——</p>
    </div>
    <GoodItem v-for="item in proList" :key="item.goods_id" :item="item"></GoodItem>
  </div>
</template>

<script>
import GoodItem from '@/components/GoodItem'
import { getHomeData } from '@/api/home'
export default {
  name: 'HomeIndex',
  components: {
    GoodItem
  },
  data () {
    return {
      bannerList: [], // 轮播
      bavList: [], // 导航
      proList: []// 商品
    }
  },
  async created () {
    const { data: { pageData } } = await getHomeData()
    this.bannerList = pageData.items[1].data
    this.bavList = pageData.items[4].data
    this.proList = pageData.items[6].data
  }
}
</script>

<style scoped lang="less">
.home{
  width: 100%;
}
.main{
width: 100%;
height: 150px;
}
.main img{
  width: 100%;
  height: 100%;
}
.guess{
  width: 100%;
  height: 30px;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

    .van-nav-bar {
      z-index: 999;
      background-color: #c21401;
      ::v-deep .van-nav-bar__title {
        color: #fff;
      }
    }
</style>
