import Vue from 'vue'
import VueRouter from 'vue-router'

import Cart from '@/views/layout/cart.vue'
import CateGory from '@/views/layout/category.vue'
import Layout from '@/views/layout/layout.vue'
import Home from '@/views/layout/home.vue'
import User from '@/views/layout/user.vue'

import '@/styles/common.less'
import store from '@/store'

const Login = () => import('@/views/login/login.vue')
const MyOrder = () => import('@/views/myorder/myorder.vue')
const Pay = () => import('@/views/pay/pay.vue')
const Prodetail = () => import('@/views/prodetail/prodetail.vue')
const SearchList = () => import('@/views/searchList/searchList.vue')
const SearchPage = () => import('@/views/searchPage/searchPage.vue')

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    { path: '/login', component: Login },
    {
      path: '/',
      component: Layout,
      redirect: '/home',
      children: [
        { path: '/home', component: Home },
        { path: '/category', component: CateGory },
        { path: '/cart', component: Cart },
        { path: '/user', component: User }
      ]
    },
    { path: '/myorder', component: MyOrder },
    { path: '/pay', component: Pay },
    { path: '/prodetail/:id', component: Prodetail },
    { path: '/searchList', component: SearchList },
    { path: '/searchPage', component: SearchPage }
  ]
})

// 所有的路由在真正被访问到之前(解析渲染对应组件页面前)，都会先经过全局前置守卫
// 只有全局前置守卫放行了，才会到达对应的页面

// 全局前置导航守卫
// to:到哪里去，到哪去的完整路由信息对象(路径，参数)
// from:从哪里来，从哪来的完整路由信息对象(路径，参数)
// next():是否放行
// (1)next()直接放行，放行到to要去的路径
// (2)next(路径)进行拦截，拦截到next里面配置的路径
const authUrls = ['/pay', '/myOrder']
router.beforeEach((to, from, next) => {
  if (!authUrls.includes(to.path)) {
    next()
    return
  }
  // 判断有误token码
  const token = store.getters.getToken
  if (token) {
    next()
  } else {
    next('/login')
  }
})

export default router
