import { getStorage, setStorage } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      userInfo: getStorage()
    }
  },
  mutations: {
    setUSerInfo (state, obj) {
      state.userInfo = obj
      setStorage(obj)
    }
  },
  actions: {
    loginOut (context) {
      // 清除用户信息
      context.commit('setUSerInfo', {})

      // 清除购物车
      context.commit('cart/addList', [], { root: true })
    }
  },
  getters: {

  }
}
