const userInfo = 'shop_user_name'
const historyKey = 'shop_user_history'

// 获取
export const getStorage = () => {
  const defaultObj = { token: '', userId: '' }
  const res = localStorage.getItem(userInfo)
  return res ? JSON.parse(res) : defaultObj
}
// 存入
export const setStorage = (obj) => {
  localStorage.setItem(userInfo, JSON.stringify(obj))
}
// 删除
export const removeStorage = () => {
  localStorage.removeItem(userInfo)
}

// 存入历史
export const setHistoryList = (arr) => {
  localStorage.setItem(historyKey, JSON.stringify(arr))
}

// 获取历史
export const getHistoryList = () => {
  const historyList = localStorage.getItem(historyKey)
  return historyList ? JSON.parse(historyList) : []
}
