import request from '@/utils/request'
// 存入
export const addCate = (goodsId, goodsNum, goodsSkuId) => {
  return request({
    url: '/cart/add',
    method: 'POST',
    data: {
      goodsId,
      goodsNum,
      goodsSkuId
    }
  })
}

// 获取购物车数量
export const getCate = () => {
  return request({
    url: '/cart/total'
  })
}

// 获取购物车列表
export const getCart = () => {
  return request({
    url: '/cart/list'
  })
}

// 购物车数量改变
export const updataCount = (goodsId, goodsNum, goodsSkuId) => {
  return request({
    url: '/cart/update',
    method: 'POST',
    data: {
      goodsId,
      goodsNum,
      goodsSkuId
    }
  })
}

// 删除购物车数据
export const delCate = (cartIds) => {
  return request({
    url: '/cart/clear',
    method: 'POST',
    data: {
      cartIds
    }
  })
}
