<template>
<div class="countBox">
  <button @click="numSum">-</button>
  <input :value="value"  @change="numChange" type="text">
  <button @click="numAdd">+</button>
</div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    numSum () {
      if (this.value <= 1) {
        return
      }
      this.$emit('input', this.value - 1)
    },
    numAdd () {
      this.$emit('input', this.value + 1)
    },
    numChange (e) {
      const num = +e.target.value
      if (isNaN(num) || num < 1) {
        e.target.value = this.value
        return
      }
      this.$emit('input', parseInt(num))
    }
  }
}
</script>

<style scoped>
.countBox{
  width: 110px;
  display: flex;
  justify-content: space-evenly;
}
.countBox button{
width: 30px;
height: 30px;
outline: none;
  border: none;
}
.countBox input{
  width: 40px;
  height: 30px;
  text-align: center;
  outline: none;
  border: none;
}
</style>
