<template>
  <div class="category" >
      <!-- 标题 -->
      <van-nav-bar title="商品列表"  @click-left="$router.back()"></van-nav-bar>
      <!-- 搜索栏 -->
      <van-search  input-align="center" shape="round" @click="$router.push('/searchPage')" placeholder="请输入搜索关键词" />
      <div class="list-box">
        <div class="leftList">
          <ul>
            <li v-for="(item, index) in leftCategorylist" :key="item.category_id" :class="{active:index===light}" @click="light=index">{{ item.name }}</li>
          </ul>
        </div>
        <div class="rightList">
          <div class="categoryItem" v-for="(item) in leftCategorylist[light]?.children" :key="item.category_id" @click="$router.push(`/searchList?categoryId=${item.category_id}`)">
            <img :src="item.image?.external_url" alt="">
            <p>{{item.name}}</p>
          </div>
          <div class="categoryItem"></div>
          <div class="categoryItem"></div>
        </div>
      </div>
  </div>
</template>

<script>
import { categoryList } from '@/api/category'
export default {
  name: 'CateGoryIndex',
  async created () {
    const { data: { list } } = await categoryList()
    this.leftCategorylist = list
  },
  data () {
    return {
      light: 0,
      leftCategorylist: []
    }
  }
}
</script>

<style scoped>
.category{
width: 100%;
height: 80vh;
}
.list-box{
  width: 100%;
  height: 100%;
  display: flex;
}
.leftList{
  width: 25%;
  height: 100%;
  border: 1px solid #908e8e;
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
  background-color: #f3f3f3;
}
.leftList ul{
width: 100%;
height: 100%;
}
.leftList li{
width: 100%;
height: 50px;
line-height: 50px;
text-align: center;
color: #444444;
font-size: 12px;
}
.active {
  color: #fb442f;
  background-color: #fff;
}
.rightList{
  width: 75%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
}
.categoryItem{
  width: calc(100%/3);
  height: 100px;
  text-align: center;
}
.categoryItem img{
width: 80%;
height: 80%;
margin-bottom: 2px;
}

</style>
